.songRow {
	/* margin-left: 20px; */
	/* padding: 20px; */
	display: flex;
	align-items: center;
	color: white;
	padding: 10px 0;
	/* border-top: 1px solid #282828; */
	/* border-bottom: 0.5px solid #282828; */
	position: relative;
}

.songRow:hover {
	/* cursor: pointer; */
	background-color: #282828;
	/* background-color: rgba(91, 87, 115, 0.2); */
	/* opacity: 0.8; */
	/* -webkit-box-shadow: 2px -2px 5px 0px rgba(255, 255, 255, 0.37);
	-moz-box-shadow: 2px -2px 5px 0px rgba(255, 255, 255, 0.37);
	box-shadow: 2px -2px 5px 0px rgba(255, 255, 255, 0.37); */
}

.songRow__info {
	margin-left: 20px;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.songRow__info > h1 {
	font-size: 16px;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.songRow__info > p {
	font-size: 14px;
	color: grey;
	margin-top: 3px;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.songRow__container {
	position: relative;
	height: 40px;
	width: 40px;
	margin-left: 10px;
}

.songRow__cover {
	height: 40px;
	width: 40px;
	/* margin-left: 10px; */
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.songRow__overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	opacity: 0;
	transition: 0.3s ease;
	/* background-color: red; */
	background-color: #2828288c;
}

.songRow__container:hover .songRow__overlay {
	opacity: 1;
}

.visible {
	opacity: 1;
}

.songRow__overlayIcon {
	color: gainsboro;
	font-size: 100px;
	position: absolute;
	/* top: 50%; */
	/* left: 50%; */
	/* transform: translate(-50%, -50%); */
	/* -ms-transform: translate(-50%, -50%); */
	transition: 0.3s ease;
	text-align: center;
}

.songRow__overlayIcon {
	color: white;
	font-size: 100px;
	position: absolute;
	height: 80% !important;
	/* top: 50%;
	left: 50%; */
	/* transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%); */
	text-align: center;
	top: 4px;
	left: 4px;
}

/* .songRow__cover:hover {
	opacity: 0.3;
	background-image: image("@material-ui/icons/PlayCircleOutline");
} */

.songRow__overlayIcon:hover {
	transition: transform 0.2s ease-in-out;
	transform: scale(1.08) !important;
	/* color: white; */
}

.songRow__overlayIcon:active {
	transition: transform 0.2s ease-in-out;
	transform: scale(1) !important;
	/* color: white; */
}

.songRow__savedTrack {
	/* padding-right: 10px; */
	margin-left: 20px;
	color: gray;
	display: flex;
	align-content: center;
}

.songRow__savedTrack:hover {
	color: #1ed15e;
}

.hidden {
	visibility: hidden;
}
